@use "../globals" as *;
@use "../parts/common" as *;

.news-page {
  @extend .wrapper;

  &__head {
    @extend .mb-lg;
  }

  &__body {
    @extend .mb-lg;
  }
  
  &__menu {
    margin-top: calc(20vh + 80px);
    display: flex;
    border-bottom: solid 1px $cBlack;

    & li {
      width: 50%;
      padding: 20px 0;
      text-align: center;
      
      @include mq(lg) {
        width: 240px;
      } 

      & a {
        @extend .font-lr;
      }
    }

    & .active {
      border-bottom: solid 3px $cBlack;
    }
  }

  &__list {
    @extend .mb-lg;

    & dt {
      margin-top: 40px;
      @include mq(lg) {
        margin-top: 50px;
      }
    }

    & dd {
      padding-bottom: 40px;
      border-bottom: solid 1px #ccc;

      @include mq(lg) {
        padding-bottom: 50px;
      }
    }
  }

  .page-nation {

    & ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }

    & li {
      padding: 0 10px;
    }

    & .active {
      text-decoration: underline;
    }

    & a:hover {
      opacity: 0.7;
    }
  }

  &__foot {
    @extend .mb-lg;
  }
}